<template>
    <div class="return">
        <ul class="routes">
            <li>
                <router-link to="/">首页</router-link>
            </li>
            <li>
                <router-link to="/order">订单中心</router-link>
            </li>
            <li>
                <router-link to="/order/return">退换货申请</router-link>
            </li>
        </ul>
        <div :class="['progress',step_bar,'row']">
            <div class="col-3 ac progress-step bold lager-font"><span :style="{color:step>=1?'#ff0077':'#ccc'}">1</span>买家申请退款</div>
            <div class="col-3 ac progress-step bold lager-font"><span :style="{color:step>=2?'#ff0077':'#ccc'}">2</span>平台处理退款</div>
            <div class="col-3 ac progress-step bold lager-font"><span :style="{color:step>=3?'#ff0077':'#ccc'}">3</span>买家退货</div>
            <div class="col-3 ac progress-step bold lager-font"><span :style="{color:step>=4?'#ff0077':'#ccc'}">4</span>退款成功</div>
        </div>
        <div class="step-box clearfix">
            <div class="step step_1 fl" v-if="step === 1">
                <table>
                    <tr>
                        <td class="tile">退款商品：</td>
                        <td>
                            <div class="clearfix">
                                <img class="fl avatar" :src="return_goods.default_img|img">
                                <p class="fl title">{{return_goods.product_name}}</p>
                            </div>
                        </td>
                    </tr>
                    <tr>
                        <td class="tile">服务类型：</td>
                        <td>
                            <label>
                                <input type="radio" v-model="type" name="returntype" value="2">
                                仅退款
                            </label>
                        </td>
                    </tr>
                    <tr v-if="type==1">
                        <td class="tile">快递单号：</td>
                        <td>
                            <input type="text" v-model="shipment_order">
                        </td>
                    </tr>
                    <tr>
                        <td class="tile">退款金额：</td>
                        <td>
                            <label class="money">
                                ￥
                            </label>
                            <input class="money" type="number" v-model="money">
                        </td>
                    </tr>
                    <tr>
                        <td class="tile">
                            退款说明：
                        </td>
                        <td>
                            <textarea name="" cols="60" rows="4" v-model="return_note" placeholder="请描述商品存在的问题/退换货原因等"></textarea>
                        </td>
                    </tr>
                    <tr>
                        <td class="tile">
                            上传图片：
                        </td>
                        <td>
                            <ul>
                                <li class="upload-img" v-for="(i,k) in imgsurl">
                                    <img :src="i" :key="k">
                                </li>
                                <li class="upload-img cursor">
                                    <label for="upload">
                                        <input id="upload" type="file" :ref="`upload`" @change="upload"
                                               accept="image/*">
                                        <img src="../../../usercenter/assets/img/order/icon_add_to.png" alt="仅可添加4张图片"
                                             title="仅可添加4张图片">
                                    </label>

                                </li>
                            </ul>
                            <span>最多上传4张图片</span>
                        </td>
                    </tr>
                    <tr>
                        <td class="tile">&nbsp</td>
                        <td>
                            <div class="cursor submit ac lager-font" @click="submit_form()">提交</div>
                        </td>
                    </tr>
                </table>
            </div>
            <div class="step step_2 fl" v-if="step === 2">
                <h1 class=" bold" style="font-size: 24px;margin-bottom: 20px;color: black">您的申请已提交，请静待平台处理</h1>
                <p style="line-height: 28px;color: #999">
                    温馨提示：平台同意或者超时未处理，系统将退款给您
                    如果平台拒绝，您可以修改申请之后再发起申请，商家会再次处理，（建议您先跟在线客服沟通情况，再重新发起申请）。
                </p>
            </div>
            <div class="step step_3 fl" v-if="step === 3">
                <h1></h1>
                <p></p>
            </div>
            <div class="fr sider">
                <p style="padding-bottom: 10px;border-bottom: 1px dotted #ccc" class="lager-font bold">订单详情</p>
                <div class="clearfix sider-info">
                    <img class="fl avatar" :src="return_goods.default_img | img"
                         alt="">
                    <p class="fl title">{{return_goods.product_name}}</p>
                </div>
                <div class="sider-form">
                    <table>
                        <tr>
                            <td class="tile">订单编号：</td>
                            <td>{{order_info.order_sn}}</td>
                        </tr>
                        <tr>
                            <td class="tile">单&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;价：</td>
                            <td>{{return_goods.goods_point}}</td>
                        </tr>
                        <tr>
                            <td class="tile">数&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;量：</td>
                            <td>{{return_goods.nums}}</td>
                        </tr>
                        <tr>
                            <td class="tile">总&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;价：</td>
                            <td>{{parseInt(return_goods.goods_point) * parseInt(return_goods.nums)}}</td>
                        </tr>
                    </table>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    let display = {
        showRecommend: false,
        nav: 'hidden',
        header: 'fault'
    };
    import {mapState} from 'vuex'
    export default {
        name: 'return',
        metaInfo: {
            title: '退换货申请',
            meta: [
                {name: 'keywords', content: '福分通'},
                {name: 'description', content: '专注企业员工福利'}
            ]
        },
        data() {
            return {
                step: 1,
                type: 2,
                return_note: '',
                money: 0,
                imgs: [],
                imgsurl: [],
                shipment_order: '',
                order_info :{},
                return_goods:{},
            }
        },
        created() {
            this.$store.dispatch('layout/Change', display);

            this.yum('api/order/getOrderDetail' , {order_sn:[this.$route.query.order_sn]}).then(res => {
                if (res.data.status === 200) {

                    this.order_info = res.data.data[0];
                    for (var i in this.order_info.goods_child){
                        if (this.order_info.goods_child[i].product_id == this.$route.query.id ){
                            this.return_goods = this.order_info.goods_child[i];
                            break;
                        }
                    }
                    // 计算当前退款状态
                    if(this.return_goods.return_status == 0){
                        this.step = 1;
                    }else if (this.return_goods.return_status == 1){
                        this.step = 2;
                    }else{
                        this.step = 3;
                    }
                }
            });

        },
        computed: {
            step_bar() {
                return `step-bar-${this.step}`
            },
            ...mapState('returnGoods',['goods'])
        },
        methods: {
            getOrderDetail(){

            },
            upload() {
                let file = this.$refs.upload.files[0];
                let URL = window.webkitURL || window.URL;
                let formData = new FormData();
                formData.append('image', file);
                this.axios.post('http://mapi.fufentong.com/www/api/file/uploads', formData).then(res => {
                    if (res.data.status === 200) {
                        if (this.imgs.length >= 4 && this.imgsurl.length >= 4) {
                            this.imgs.splice(0, 1);
                            this.imgsurl.splice(0, 1)
                        }
                        this.imgs.push(res.data.data);
                        this.imgsurl.push(URL.createObjectURL(file));
                    }
                }).catch(
                    err => {
                        if (err) {
                            this.bus.$emit('notice', '请检查文件是否过大', 1500);
                        }
                    }
                )
            },
            submit_form(){
                var formData = {
                    type:this.type,
                    img:this.imgs,
                    order_sn:this.order_info.order_sn,
                    return_note:this.return_note,
                    goods_id:[this.return_goods.product_id],
                    num:this.return_goods.nums,
                    shipment_order:this.shipment_order,
                    username:this.order_info.shouhuo_name,
                    return_mobile:this.order_info.shouhuo_moblie,
                };
                if(!this.shipment_order&&this.type==2){
                    this.bus.$emit('notice','未填写快递单号！');
                    return
                }
                if (!this.return_note){
                    this.bus.$emit('notice','未添加退款说明！');
                    return
                }
                // if(this.imgs.length === 0){
                //     this.bus.$emit('notice','未上传图片！');
                //     return
                // }
                this.yum('api/order_return/setMemberReturnApplys' , formData).then(res => {
                    if (res.data.status === 200) {
                        this.step = 2;
                    }
                });
            }
        }
    };
</script>

<style lang="stylus" scoped>
    .return
        min-width 100%
        margin 40px 0 0 0
        position relative
        &.router
            margin-left 0

    ul.routes
        position relative
        li
            position relative
            display inline-block
            &:after
                content '>'
                margin 0 10px
            &:last-child:after
                display none

    .progress
        margin 20px auto 0
        width 990px
        height 40px
        .progress-step
            height 40px
            line-height 40px
            color white
            span
                display inline-block
                font-size 18px
                width 28px
                height 28px
                line-height 28px
                margin-right 5px
                background-color white
                border-radius 50%
                color #ccc

    .step-box
        margin-top 10px
        position relative
        border 1px solid #ccc
        height 640px
        margin-bottom 100px

    .step
        padding 20px
        width 740px
        height 640px
        table
            width 100%
            tr
                margin-bottom 20px
                display block
            td
                position relative
                div.submit
                    width 150px
                    height 30px
                    line-height 30px
                    background-color #ff0077
                    color white
                    border-radius 4px
            td.tile
                width 70px
                vertical-align top
                color black
            img.avatar
                width 80px
                height 80px
                border 1px solid #ccc
            p.title
                width 440px
                color black
                line-height 24px
                margin-left 20px
            select, input[type=number], input[type=text]
                width 190px
                height 40px
                &::-webkit-outer-spin-button, &::-webkit-inner-spin-button
                    -webkit-appearance: none;
            textarea
                resize none
                padding 5px
            label.money
                display inline-block
                position absolute
                top 10px
                left 5px
            input.money
                padding-left 20px
            ul
                display inline-block
            li.upload-img
                width 80px
                height 80px
                display inline-block
                margin-right 10px
                position relative
                border 1px solid #ccc
                img
                    width 60px
                    height 60px
                    display block
                    margin 10px
            #upload
                display none

    .sider
        height 100%
        width 240px
        padding 20px
        border-left 1px solid #ccc
        .sider-info
            margin-top 10px
        .avatar
            width 60px
            height 60px
            display block
        p.title
            width 130px
            height 50px
            margin-left 5px
            overflow hidden
            font-size 12px
        .sider-form
            position relative
            font-size 12px
            table
                width 100%
                margin-top 20px
            td
                color #999
                font-size 12px
                &.tile
                    width 60px

    .step-bar-1
        background-image url("../../assets/img/return/bg_1.png")

    .step-bar-2
        background-image url("../../assets/img/return/bg_2.png")

    .step-bar-3
        background-image url("../../assets/img/return/bg_3.png")
</style>
